import React, { Component } from 'react';
import { getAction,postAction } from '@/axios';
import { message, Input, DatePicker, Button, Table, Steps, Popover, Timeline,Divider,Modal} from 'antd';
import moment from 'moment';
import styles from './index.module.less';
import { withRouter } from 'react-router-dom';
//展开的步骤条
// const expandSteps = (record, index, indent, expanded) => {
const expandSteps = (data) => {        
    let stepsData = data || {current:0,steps:[]};
    // console.log(stepsData);
    if(stepsData.steps.length === 0){
        return <div style={{textAlign:"center"}}>暂无数据</div>
    }
    return (
        <Steps current={stepsData.current} progressDot={true}>
            {
                stepsData.steps.map((item,index) => (
                    <Steps.Step key={index} title={item.title} description={
                        <Popover
                            content={
                                <Timeline>
                                    {
                                        item.toolTips?item.toolTips.map((el,index) => (
                                            <Timeline.Item key={index}>
                                                <p style={{color:"rgba(0,0,0,0.65)"}}>
                                                    {el.job}
                                                    <span style={{color:"#000000",padding:"0 5px"}}>{el.name}</span>
                                                    {el.status===1?
                                                        <><span style={{color:"#52C41A"}}>同意</span>该审批</>:
                                                        <span style={{color:"#FA8C16"}}>等待审批</span>
                                                    }
                                                </p>
                                                {/* 同意时才有审批意见和时间 */}
                                                {
                                                    el.status===1?
                                                    <>
                                                        <p style={{color:"rgba(0,0,0,0.65)"}}>审批意见：<span>{el.comments}</span></p>
                                                        <p style={{color:"rgba(0,0,0,0.65)"}}>{moment(el.time).format("YYYY-MM-DD HH:mm:ss")}</p>
                                                    </>:null
                                                } 
                                            </Timeline.Item>
                                        )):null
                                    }
                                </Timeline>
                            }
                        >
                            <span style={{cursor:"pointer",color:"#3F54A2"}}>{item.detail}{item.detail?item.approveType===1?'(或签)':'(会签)':''}</span>
                        </Popover>
                    } />
                ))
            }
        </Steps>
    )
};

const statusList = [
    {label:"审核中",value:1,color:"#FA541C"},
    {label:"审核通过",value:2,color:"rgba(65,100,228,1)"},
    {label:"审核退回",value:3,color:"rgba(250,84,28,0.3)"},
    {label:"已超时",value:4,color:"rgba(250,140,22,0.3)"},
    {label:"待编辑",value:5,color:"rgba(82,196,26,1)"},
    {label:"已完成",value:5,color:"rgba(82,196,26,1)"},
];

class TodoItems extends Component {
    constructor(props){
        super(props);
        this.state = {
            nodeTaskName:"",//事项名称
            receiveDate:[],//接收日期
            itemInfo:{},//当前事项信息
            expandTableRowKeys:[],//查看详情项的key
            queryParams:{},
            columns:[
                {
                    title:"序号",
                    dataIndex:"sortIndex",
                    width:"80px"
                },{
                    title:"事项名称",
                    dataIndex:"nodeTaskName",
                    width:"360px"
                },{
                    title:"接收时间",
                    dataIndex:"taskStartTime",
                    width:"160px",
                    render:(text)=>{
                        return moment(text).format("YYYY-MM-DD")||"";
                    }
                },{
                    title:"完成时间",
                    dataIndex:"taskEndTime",
                    width:"160px",
                    render:(text)=>{
                        return moment(text).format("YYYY-MM-DD")||"";
                    }
                },{
                    title:"状态",
                    dataIndex:"status",
                    width:"130px",
                    render:(text) => {
                        const statusInfo = statusList.find(item => item.label === text);
                        return statusInfo?<React.Fragment><i style={{display:"inline-block",width:8,height:8,borderRadius:"50%",marginRight:10,background:statusInfo.color}}></i>{statusInfo.label}</React.Fragment>:"未知状态待添加"
                    }
                },{
                    title:"办理结果",
                    dataIndex:"userConclusionsType",
                    width:"80px",
                    render:(text) => <span style={{color:text==="退回"?"#F5222D":""}}>{text}</span>
                },{
                    title:"操作",
                    width:"180px",
                    align:'center',
                    render:(text,record) => {                        
                        return(
                            <React.Fragment>
                                <span className={styles.link} onClick={()=>{this.goToHandle(record)}}>查看事项</span>
                                <Divider type="vertical"/>
                                {
                                    this.state.expandTableRowKeys.length>0 && this.state.current.taskId === record.taskId?
                                    <span className={styles.link} onClick={()=>this.onExpandTable(false,record)}>收起进度</span>:
                                    <span className={styles.link} onClick={()=>this.onExpandTable(true,record)}>查看进度</span>
                                }
                                
                            </React.Fragment>
                        )
                    }
                }
            ],
            dataSource:[],
            pagination:{
                current:1,
                pageSize:10,
                total:0,
                showTotal:(total)=>`共${total}条`,
                showQuickJumper:true,
                // showSizeChanger:true,
                size:"small"
            },
            current:null
        }
    }
    componentDidMount(){
        this.queryOverTaskList({});
        document.addEventListener("keydown",this._onkeydown);
    }
    _onkeydown = (e) => {
        if(e.keyCode === 13){
            this.handleSearch();
        }
    }
    componentWillUnmount(){
        document.removeEventListener("keydown",this._onkeydown)
    }
    //查询已办列表
    queryOverTaskList(data,page){
        // 空参数删除
        for(let i in data){
            if(!data[i]){
                delete data[i]
            }
        }
        let current = 1;
        if(page==="page"){
            current = this.state.pagination.current;
        }
        let url = `/ybt/wf/approveTask/queryOverTaskList?pageNum=${current}&pageSize=${this.state.pagination.pageSize}`;
        postAction(url,data).then(res => {
            if(res.success){
                //设置分页数据
                let pagination = JSON.parse(JSON.stringify(this.state.pagination));
                pagination.total = res.obj.total;
                pagination.current = res.obj.page;
                pagination.pageSize = res.obj.pageSize;

                //设置表格数据
                let dataSource = res.obj.rows||[];
                dataSource.forEach((item,index)=>{
                     item.sortIndex = index+1;
                });
                this.setState({
                    dataSource,
                    pagination
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    //查看详情或者展开
    onExpandTable = (expanded, record) => {
        if(expanded){
            this.getExpandedRowsDetail(record,record.sortIndex-1);
        }
        this.setState({
            expandTableRowKeys:expanded?[record.sortIndex]:[],
            current:record
        })
    }
    //查看事项
    goToHandle = (record) => {
        if(record.nodeTaskType===1){//填报计划审核
            this.props.history.push("/auditManagement/lookUp?id="+record.processNodeTaskId)
        }else{
            //创建表单类型processtype=2
            Modal.confirm({
                title:null,
                icon:null,
                content:<iframe scrolling='auto' title='预览' style={{border:'none',width:'100%',height:'500px'}} src={`http://${window.location.host}/ybt/form/dl/result/view?${record.processType === 2 ? 'formId':'formTaskId'}=${record.businessId}`}></iframe>,
                okText:'确定',
                cancelText:"下载",
                onCancel:()=>{
                    window.open(`http://${window.location.host}/ybt/form/dl/export?formTaskId=${record.taskId}`)
                },
                width:1000,
            })
        }
        
        // this.props.history.push("/auditManagement/audit?id="+record.process_node_task_id)
        //填报
    }
    handleReset=()=>{
        const filterValue = {
            taskStartTimeStart:"",
            taskStartTimeEnd:"",
            keyWord:""
        }
        this.setState({
            queryParams:filterValue,
            nodeTaskName:"",
            receiveDate:[]
        })
        // console.log(filterValue);
        this.queryOverTaskList(filterValue);
    }
    //获取行的详情内容
    getExpandedRowsDetail=(record,index)=>{ 
        // console.log(record);
        let data={
            taskId:record.taskId,
            processId:record.processId
        }
        getAction('/ybt/wf/approveTask/queryOverTaskProcess',data).then(res => {
            if(res.success){
                let dataSource = JSON.parse(JSON.stringify(this.state.dataSource));
                //setps
                let stepsData = {current:res.obj.current,steps:res.obj.setps}
                dataSource[index].stepsData =stepsData;
                this.setState({
                    dataSource
                });
            }else{
                message.error(res.obj)
            }
        })
    }
    //事项名称
    handleChangeInput = (e) => {
        this.setState({
            nodeTaskName:e.target.value
        })
    }
    //接收日期
    handleChangeDate = (dates,dateString) => {
        // console.log(dates,dateString);
        this.setState({
            receiveDate:dates
        })
    }
    //搜索
    handleSearch = () => {
        // const date = this.state.receiveDate.length>0?[moment(this.state.receiveDate[0]).format("YYYY/MM/DD"),moment(this.state.receiveDate[1]).format("YYYY/MM/DD")]:[];
        let taskStartTime,taskEndTime="";
        if(this.state.receiveDate.length>0){
            taskStartTime = moment(this.state.receiveDate[0]).format("YYYY-MM-DD")||"";
            taskEndTime=moment(this.state.receiveDate[1]).format("YYYY-MM-DD")||"";
        }
        const filterValue = {
            taskStartTime,
            taskEndTime,
            nodeTaskName:this.state.nodeTaskName
        }
        this.setState({
            queryParams:filterValue
        })
        // console.log(filterValue);
        this.queryOverTaskList(filterValue);
    }
    //分页、排序、筛选变化时触发
    onChange=(pagination, filters, sorter)=>{
        //  console.log(pagination, filters, sorter);
        this.setState({
            pagination
        },()=>{
            this.queryOverTaskList(this.state.queryParams,"page");
        })
    }
    render() {
        return (
            <div className={styles.container}>
                    <div className={styles.card}>
                        <div className={styles.cardTitle}>全部已办</div>
                        <div className={styles.filterBox}>
                            <div className={styles.filterItem}>
                                事项名称：
                                <Input style={{width:220}} value={this.state.nodeTaskName} onChange={this.handleChangeInput} placeholder="请输入事项名称"/>
                            </div>
                            <div className={styles.filterItem}>
                                接收时间：
                                <DatePicker.RangePicker  style={{width:320}} value={this.state.receiveDate} format={"YYYY-MM-DD"} onChange={this.handleChangeDate}/>
                            </div>
                            <div>
                                <Button ghost type="primary" icon="search" style={{marginRight:12}} onClick={this.handleSearch}>搜索</Button>
                                <Button icon="sync" style={{marginRight:12}} onClick={this.handleReset}>重置</Button>
                            </div>
                        </div>
                        <Table
                            rowKey="sortIndex"
                            columns={this.state.columns}
                            dataSource={this.state.dataSource}
                            expandedRowRender={record => expandSteps(record.stepsData)}//展开行的内容
                            expandedRowKeys={this.state.expandTableRowKeys}//要展开的行
                            onExpand={this.onExpandTable}//点击展开图标时触发

                            pagination={this.state.pagination}
                            onChange={this.onChange}
                        />
                    </div>
            </div>
        )
    }
}

export default withRouter(TodoItems)