import React from 'react';
import { getAction } from '@/axios';
import { Tabs, Badge,message } from 'antd';
import styles from './index.module.less'
import TodoItems from './todoItems';
import DoneItems from './doneItems';

const { TabPane } = Tabs;

const colorStyle1 = { backgroundColor: '#3F54A2', marginLeft: "10px", marginTop: "-5px" };
const colorStyle2 = { backgroundColor: 'rgba(0,0,0,0.25)', marginLeft: "10px", marginTop: "-5px" };

class Items extends React.Component{
    state = {
        activeKey: '0',
        todoBusiness:0,//待办业务
        doneBusiness:0//已办业务
    }
    componentDidMount () {
        let currentKey = window.location.href.split('flag=')[1];
        let activeKey = currentKey ? currentKey : "0";
        this.setState({
            activeKey
        })
        //查询待办事项
        this.getTaskNumber();
    }
    //查询待办事项
    getTaskNumber=()=>{
        getAction('ybt/wf/approveTask/getTaskNumber',{}).then(res => {
            if(res.success){
                // console.log(dataSource);
                this.setState({
                    doneBusiness:res.obj[0],
                    todoBusiness:res.obj[1]
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    tabChange = (activeKey) => {
        this.setState({
            activeKey
        })
    }
    render(){
        let item1 = this.state.activeKey === '0' ? colorStyle1 : colorStyle2;
        let item2 = this.state.activeKey === '1' ? colorStyle1 : colorStyle2;
        return (
            <Tabs activeKey={this.state.activeKey} onChange={this.tabChange} className={styles.tabs} forceRender={false}>
                <TabPane 
                    key="0"
                    tab={
                        <span>
                            待办事项
                            <Badge 
                                count={this.state.todoBusiness} 
                                style={item1} 
                            />
                        </span>
                    } 
                >
                    <TodoItems/>
                </TabPane>
                <TabPane
                    key="1"
                    tab={
                        <span>
                            已办事项
                            <Badge 
                                count={this.state.doneBusiness} 
                                style={item2} 
                            />
                        </span>
                    }
                >
                    <DoneItems/>
                </TabPane>
            </Tabs>
        )
    }
}

export default Items