import React, { Component } from 'react';
import {getAction, postAction } from '@/axios';
import { message, Input, DatePicker, Button, Table, Steps, Popover, Divider, Timeline} from 'antd';
import moment from 'moment';
import { withRouter } from 'react-router-dom';
import styles from './index.module.less';

//展开的步骤条
const auditStatusList = [
    {color:"#52C41A",text:"通过"},
    {color:"#FA541C",text:"退回"},
    {color:"#52C41A",text:"审核中"},
    {color:"orange",text:"待审批"}
];
function findStatus(text){
    let arr = auditStatusList.find(item => item.text===text);
    if(arr){
        return arr
    }
    return false;
}
const expandSteps = (stepsData = {current:0,setps:[]}) => {
    if(!stepsData.setps){
        return <div style={{textAlign:"center"}}>暂无数据</div>
    }
    return (
        <Steps current={stepsData.current} progressDot={true} style={{marginLeft:"-60px"}}>
            {
                stepsData.setps.map((item,index) => (
                    <Steps.Step key={index} title={item.title} description={
                        <Popover
                            content={
                                <Timeline>
                                    {
                                        item.toolTips?item.toolTips.map((el,index) => (
                                            <Timeline.Item key={index}>
                                                <p style={{color:"rgba(0,0,0,0.65)"}}>
                                                    {el.job}
                                                    <span style={{color:"#000000",padding:"0 5px"}}>{el.name}</span>
                                                    {
                                                        findStatus(el.status)?<span style={{color:findStatus(el.status).color}}>{findStatus(el.status).text}</span>:"状态未知"
                                                    }
                                                </p>
                                                {
                                                    (el.status===2 || el.status===3)?
                                                    <>
                                                        <p style={{color:"rgba(0,0,0,0.65)"}}>审批意见：<span>{el.comments}</span></p>
                                                        <p style={{color:"rgba(0,0,0,0.65)"}}>{moment.unix(el.time/1000).format("YYYY/MM/DD HH:mm:ss")}</p>
                                                    </>:null
                                                } 
                                            </Timeline.Item>
                                        )):null
                                    }
                                </Timeline>
                            }
                        >
                            <span style={{cursor:"pointer",color:"#3F54A2"}}>{item.detail}{item.detail?item.approveType===1?'(或签)':'(会签)':''}</span>
                        </Popover>
                    } />
                ))
            }
        </Steps>
    )
}

class TodoItems extends Component {
    constructor(props){
        super(props);
        this.state = {
            nodeTaskName:"",//事项名称
            receiveDate:[],//接收日期时间
            queryParams:{},
            itemInfo:{},//当前事项信息
            expandTableRowKeys:[],//查看详情项的key
            columns:[
                {
                    title:"序号",
                    dataIndex:"sortIndex",
                    width:"80px"
                },{
                    title:"事项名称",
                    dataIndex:"node_task_name",
                    width:"400px"
                },{
                    title:"接收时间",
                    dataIndex:"task_start_time",
                    width:"160px"
                },{
                    title:"操作",
                    width:"180px",
                    align:'center',
                    render:(text,record) => {
                        return(
                            <React.Fragment>
                                <span className={styles.link} onClick={()=>{this.goToHandle(record)}}>前往办理</span>
                                <Divider type="vertical"/>
                                {
                                    this.state.expandTableRowKeys.length>0 && this.state.current.taskId === record.taskId?
                                    <span className={styles.link} onClick={()=>this.onExpandTable(false,record)}>收起进度</span>:
                                    <span className={styles.link} onClick={()=>this.onExpandTable(true,record)}>查看进度</span>
                                }
                                
                            </React.Fragment>
                        )
                    }
                }
            ],
            dataSource:[],
            pagination:{
                current:1,
                pageSize:10,
                total:0,
                showTotal:(total)=>`共${total}条`,
                showQuickJumper:true,
                // showSizeChanger:true,
                size:"small"
            },
            stepData:{},
            current:null
        }
    }
    componentDidMount(){
        this.getWaitProceeList({});
        document.addEventListener("keydown",this._onkeydown);
    }
    _onkeydown = (e) => {
        if(e.keyCode === 13){
            this.handleSearch();
        }
    }
    componentWillUnmount(){
        document.removeEventListener("keydown",this._onkeydown)
    }
    //查询待办列表
    getWaitProceeList=(data,page)=>{
        // console.log(data);
        // 空参数删除
        for(let i in data){
            if(!data[i]){
                delete data[i]
            }
        }
        let current = 1;
        if(page==="page"){
            current = this.state.pagination.current;
        }
        let url = `/ybt/wf/approveTask/getWaitProceeList?pageNum=${current}&pageSize=${this.state.pagination.pageSize}`;
        postAction(url,data).then(res => {
            if(res.success){
                //设置分页数据
                let pagination = JSON.parse(JSON.stringify(this.state.pagination));
                pagination.total = res.obj.total;
                pagination.current = res.obj.page;
                pagination.pageSize = res.obj.pageSize;

                //设置表格数据
                let dataSource = res.obj.rows||[];
                dataSource.forEach((item,index)=>{
                     item.sortIndex = index+1;
                });
                this.setState({
                    dataSource,
                    pagination
                })
            }else{
                message.error(res.obj)
            }
        })
    }
     //搜索
     handleSearch = () => {
        // const date = this.state.receiveDate.length>0?[moment(this.state.receiveDate[0]).format("YYYY/MM/DD"),moment(this.state.receiveDate[1]).format("YYYY/MM/DD")]:[];
        // console.log(this.state.receiveDate);
        let taskStartTimeStart,taskStartTimeEnd="";
        if(this.state.receiveDate.length>0){
            taskStartTimeStart = moment(this.state.receiveDate[0]).format("YYYY-MM-DD HH:mm:ss")||"";
            taskStartTimeEnd=moment(this.state.receiveDate[1]).format("YYYY-MM-DD HH:mm:ss")||"";
        }
        const filterValue = {
            taskStartTimeStart,
            taskStartTimeEnd,
            keyWord:this.state.nodeTaskName
        }
        this.setState({
            queryParams:filterValue
        })
        // console.log(filterValue);
        this.getWaitProceeList(filterValue);
    }
    handleReset=()=>{
        const filterValue = {
            taskStartTimeStart:"",
            taskStartTimeEnd:"",
            keyWord:""
        }
        this.setState({
            queryParams:filterValue,
            nodeTaskName:"",
            receiveDate:[]
        })
        // console.log(filterValue);
        this.getWaitProceeList(filterValue);
    }
    //分页、排序、筛选变化时触发
    onChange=(pagination, filters, sorter)=>{
        this.setState({
            pagination
        },()=>{
            this.getWaitProceeList(this.state.queryParams,"page");
        })
    }
    //前往办理
    goToHandle = (record) => {
        if(record.node_task_type===3){//表单填报
            getAction(`/ybt/form/task/info`, {//如果超时则不跳转
                formTaskId: record.form_task_id
            }).then(res=>{
                if(res.success){
                    if(res.obj.taskStatus===5){
                        message.info("该计划已超时,不可填报");
                        return false;
                    }else{
                        this.props.history.push(`/myFillingTask/step2?id=${record.form_task_id}&type=fill`)
                    }
                }else{
                    message.error(res.obj)
                }
            })
            // this.props.history.push("/myFillingTask/step2?id="+record.form_task_id+"&type=fill")
        }else if(record.node_task_type===5){//系统通知
            console.log("系统通知，无跳转")
        }else{//审核
            this.props.history.push("/auditManagement/audit?id="+record.process_node_task_id)
        }
    }
    //查看详情或者展开行
    onExpandTable = (expanded, record) => {
        // console.log(expanded,record)
        if(expanded){
            this.getExpandedRowsDetail(record,record.sortIndex-1);
        }
        this.setState({
            expandTableRowKeys:expanded?[record.sortIndex]:[],
            current:record
        });
    }
    //获取行的详情内容
    getExpandedRowsDetail=(record,index)=>{
        // console.log(record);
        let data={
            processId:record.process_id,
            taskId:record.task_id,
        }
        getAction('/ybt/planTask/queryProcessMap',data).then(res => {
            if(res.success){
                this.setState({
                    stepData:res.obj
                });
            }else{
                message.error(res.obj)
            }
        })
    }
    //事项名称
    handleChangeInput = (e) => {
        this.setState({
            nodeTaskName:e.target.value
        })
    }
    //接收日期
    handleChangeDate = (dates,dateString) => {
        this.setState({
            receiveDate:dates
        })
    }
   
    render() {
        return (
            <div className={styles.container}>
                    <div className={styles.card}>
                        <div className={styles.cardTitle}>全部待办</div>
                        <div className={styles.filterBox}>
                            <div className={styles.filterItem}>
                                事项名称：
                                <Input style={{width:220}} value={this.state.nodeTaskName} onChange={this.handleChangeInput} placeholder="请输入事项名称"/>
                            </div>
                            <div className={styles.filterItem}>
                                接收时间：
                                <DatePicker.RangePicker  style={{width:320}} value={this.state.receiveDate} format={"YYYY-MM-DD HH:mm:ss"} onChange={this.handleChangeDate}/>
                            </div>
                            <div>
                                <Button ghost type="primary" icon="search" style={{marginRight:12}} onClick={this.handleSearch}>搜索</Button>
                                <Button icon="sync" style={{marginRight:12}} onClick={this.handleReset}>重置</Button>
                            </div>
                        </div>
                        <Table
                            rowKey="sortIndex"
                            columns={this.state.columns}
                            dataSource={this.state.dataSource}
                            expandedRowRender={record => expandSteps(this.state.stepData)}//展开行的内容
                            expandedRowKeys={this.state.expandTableRowKeys}//要展开的行
                            onExpand={this.onExpandTable}//点击展开图标时触发

                            pagination={this.state.pagination}
                            onChange={this.onChange}
                        />
                    </div>
            </div>
        )
    }
}


export default withRouter(TodoItems)